a {
  text-decoration: none;
}

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

img {
  max-width: 100%;
  height: auto;
}

.page-body {
  padding: 0;
  margin: 0;
  background-color: $medium-gray;
  font-family: $font-family-main;
  font-size: 16px;
  line-height: 24px;
  color: $white;
}

.social,
.pets__list,
.slider,
.breed__list,
.advantages__list {
  padding: 0;
  margin: 0;
  list-style: none;
}
